.gallery{
  margin-top: 1vh;
  margin-bottom: 1vh;
  display: flex;
  width: 100%;
  height: 40vh;
.item{
  flex:auto;
  background-position: center;
  background-size: cover;
  transition: all 0.3s;
  &:hover{
    flex:5;
  }
}
}.item-1{
  background-color:pink
}
.item-2{
  background-color:blue
}
.item-3{
  background-color:yellow
}
.item-4{
  background-color:green
}
.item-5{
  background-color:red
}
